


















import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import { IType } from '@/types/interface';

@Component
export default class SelectionPage extends Vue {
  @Action('getTypeMenu', { namespace: 'testKind' }) getTypeMenu!: () => void;
  @State('typeMenu', { namespace: 'testKind' }) typeMenu!: IType[];

  list: string[] = [];

  get filterSelected() {
    return this.typeMenu.filter((d: IType) => this.list.indexOf(d.typename) >= 0);
  }

  get filterList() {
    return this.typeMenu.filter((d: IType) => this.list.indexOf(d.typename) < 0);
  }

  created() {
    this.getTypeMenu();
  }

  select(item: IType) {
    this.$router.push({
      path: '/main/information/content',
      query: { typename: item.typename, title: item.title },
    });
  }
}
